// js
import React, {Component} from "react";
import gsap, {ScrollTrigger} from "gsap/all";

// css
import "./Styles/Reset.css";
import "./Styles/App.sass";

import Header from "./Components/Header";
import Hero from "./Components/Hero";
import About from "./Components/About";
import Services from "./Components/Services";
import Testimonials from "./Components/Testimonials";
import Contact from "./Components/Contact";
import Footer from "./Components/Footer";

export default class App extends Component {
	constructor() {
		super();

		this.headerComponent = React.createRef();
		this.heroComponent = React.createRef();
		this.aboutComponent = React.createRef();
	}

	componentDidMount() {
		gsap.registerPlugin(ScrollTrigger);

		const headerComponent = this.headerComponent.current.header.current,
			headerLogo = headerComponent.childNodes[0];
		const heroComponent = this.heroComponent.current.hero.current;
		// const aboutComponent = this.aboutComponent.current.about.current;

		gsap.set(headerLogo, {opacity: 0});

		gsap.to(headerComponent, {
			scrollTrigger: {
				trigger: heroComponent,
				start: "bottom 20%",
				onEnter: () => {
					headerComponent.classList.add("active");
					gsap.to(headerLogo, {opacity: 1, duration: 0.25});
				},
				onLeaveBack: () => {
					headerComponent.classList.remove("active");
					gsap.to(headerLogo, {opacity: 0, duration: 0.25});
				},
			},
		});
	}

	render() {
		return (
			<>
				<Header ref={this.headerComponent} />
				<>
					<Hero ref={this.heroComponent} />
					<About ref={this.aboutComponent} />
					<Services />
					<Testimonials />
					<Contact />
					<Footer />
				</>
			</>
		);
	}
}
