import React, {Component} from "react";
import gsap, {ScrollTrigger} from "gsap/all";

export default class Contact extends Component {
	constructor() {
		super();
		this.contact = React.createRef();
		this.sectionTitle = React.createRef();
		this.sectionText = React.createRef();
	}

	componentDidMount() {
		gsap.registerPlugin(ScrollTrigger);

		const mainAnimation = gsap.fromTo(
			[this.sectionTitle.current, this.sectionText.current],
			{opacity: 0, yPercent: 50},
			{
				opacity: 1,
				yPercent: 0,
			}
		);

		ScrollTrigger.create({
			trigger: this.contact.current,
			animation: mainAnimation,
			start: "top 25%",
			toggleActions: "play none none none",
			once: true,
		});
	}
	render() {
		return (
			<div className="section pink" id="contact" ref={this.contact}>
				<div className="background"></div>
				<div className="inner">
					<div className="inner-wrapper main-second">
						<div className="main">
							<div className="main-container">
								<h2 className="section-title" ref={this.sectionTitle}>
									Contact
								</h2>
								<p className="section-text" ref={this.sectionText}>
									Lorem ipsum dolor sit amet, consectetur adipiscing elit. Morbi
									non vestibulum ligula. Pellentesque habitant morbi tristique
									senectus et netus et malesuada fames ac turpis egestas. Ut
									feugiat leo vitae cursus tristique. Praesent eu lorem eget ex
									pharetra gravida. Aliquam erat volutpat. Sed euismod sapien in
									condimentum iaculis. In suscipit libero.
								</p>
							</div>
							<div className="second">
								<div className="second-container"></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}
